.ItSelectedList{
    display: flex;
    flex-direction: column;
    width: 650px;
    min-height: 300px;
    z-index: 376;
    background-color: white;
    padding: 20px;
    position: absolute;
    border-radius: 30px;
    top: 0;
    transform: translate(420px, 210px);
    transition: all 1.5s ease-in-out;
}
.bottomField{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
}
.details{
    width: 50%;
    height: 100%;
    display: flex;
    padding-left: 30px;
    align-items: center;
    justify-content: center;
    border: 0.5px solid #e6e4e4;
}
.details a{
    color: black !important;

}
.details h3{
    font-size: 20px !important;
}
.details:first-child{
    border-radius: 0px 0px 0px 20px;
}
.details:last-child{
    border-radius: 0px 0px 20px 0px;
}
.cartIcon,
.coinIcon{
    font-size: 20px;
}
.FooterList{
    width: 100%;
    height: 80px;
    font-size: 15px;
    font-weight: 900;
    background-color: #01e281;
    padding-top: 10px;
    color: #173850;
    border-radius: 0px 0px 20px 20px;
    text-align: center;
}
.after{
    position: absolute;
    top: -36px;
    right: 20px;
    font-size: 40px;
    color: #01e281;
}
/* Media Queries */
@media (max-width: 1200px) {
    .ItSelectedList {
        width: 80%; /*      */
        padding: 15px; /*   */
        position: absolute;
        top: 0;
        right: -500px;
        animation: a 1s 0.5s ease-in-out 1 alternate forwards;
    }
    @keyframes a {
        from{
            right: -500px;
        }
        to{
            right: 500px;
        }

    }

    .details {
        width: 100%; /*      */
        padding-left: 15px; /*    */
    }

    .details h3 {
        font-size: 18px; /*    */
    }

    .cartIcon,
    .coinIcon {
        font-size: 18px; /*    */
    }
    .after{
        display: none;
    }
}


@media (max-width: 768px) {
    .ItSelectedList {
        width: 90%; 
        min-height: auto;
        padding: 10px;
        position: absolute;
        top: -116px;
        animation: a 1s 0.5s ease-in-out 1 alternate forwards;
    }
    @keyframes a {
        from{
            right: -500px;
        }
        to{
            right: 438px;
        }

    }
    .bottomField {
        height: auto; /*    */
        flex-direction: column; /*     */
    }

    .details {
        width: 100%; /*      */
        padding-left: 10px; /*    */
        justify-content: flex-start; /*     */
    }

    .details h3 {
        font-size: 16px; /*     */
    }

    .FooterList {
        height: auto; /*    */
        font-size: 14px; /*    */
    }

    .after {
        display: none;/*   */
    }
}

@media (max-width: 480px) {
    .ItSelectedList {
        width: 95%; 
        padding: 5px; 
        position: absolute;
        top: -116px;
        animation: a 1s 0.5s ease-in-out 1 alternate forwards;
    }
    @keyframes a {
        from{
            right: -500px;
        }
        to{
            right: 438px;
        }

    }

    .bottomField {
        height: auto; /*    */
        flex-direction: column; /*     */
    }

    .details {
        padding-left: 5px; /*     */
        justify-content: flex-start; /*     */
    }

    .details h3 {
        font-size: 14px; /*     */
    }

    .FooterList {
        height: auto; /*    */
        font-size: 12px; /*     */
    }

    .after {
       display: none;/*    */
    }
}